/* General Styles */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f0f0f0;
  transition: background-color 0.3s ease;
}

.game-container {
  text-align: center;
  padding: 30px;
  /* background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #9f7928 30%, #8A6E2F 40%, transparent 80%),
                radial-gradient(ellipse farthest-corner at left top, #FFFFFF 0%, #FFFFAC 8%, #D1B464 25%, #5d4a1f 62.5%, #5d4a1f 100%); */
}

.card-container {
  display: flex;
  justify-content: center;
  gap: 30px; /* Increased space between cards */
  margin-top: 30px;
}

.card {
  width: 150px;  /* Increased width */
  height: 225px; /* Increased height */
  perspective: 1200px; /* Stronger 3D effect */
  cursor: pointer;
}

.card-inner {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.6s;
}

/* Flip Animation */
.flipped .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px; /* Increased font size */
  border: 2px solid #ccc;
  border-radius: 12px; /* More rounded corners */
  transition: background-color 0.3s;
}

/* Front of the Card */
.card-front {
  /* background-color: #3498db; */
  /* background: linear-gradient(45deg, gold, silver); */
  background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #9f7928 30%, #8A6E2F 40%, transparent 80%),
  radial-gradient(ellipse farthest-corner at left top, #FFFFFF 0%, #FFFFAC 8%, #D1B464 25%, #5d4a1f 62.5%, #5d4a1f 100%);
  color: white;
}

/* Back of the Card (Default Blank) */
.card-back {
  background-color: #e74c3c;
  color: white;
  transform: rotateY(180deg);
}

/* Special Styling for the King Card */
.king-card {
  background: linear-gradient(45deg, gold, silver);
  color: black;
  font-weight: bold;
  text-shadow: 2px 2px 4px #000;
  font-size: 32px; /* Larger font for King card */
}

/* Message Styling */
.message {
  margin-top: 20px;
  font-size: 20px;
  font-weight: bold;
}

/* Restart Button */
.restart-button {
  margin-top: 20px;
  padding: 12px 24px;
  font-size: 18px;
  cursor: pointer;
  background-color: #2ecc71;
  color: white;
  border: none;
  border-radius: 8px;
  transition: background-color 0.3s;
}

.restart-button:hover {
  background-color: #27ae60;
}


.king-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}








