body {
  background-color: #f0f2f5;
  font-family: 'Arial', sans-serif;
}

.game-container {
  text-align: center;
  padding: 20px;
}

.atm-card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #e0e4e8;
}

.atm-card-form {
  background: linear-gradient(145deg, #1e2a38, #30415d);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
  width: 350px;
  color: white;
}

.form-title {
  margin-bottom: 20px;
  font-size: 24px;
}

.form-field {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.form-field label {
  margin-bottom: 5px;
  font-weight: bold;
}

.form-field input {
  padding: 10px;
  border-radius: 5px;
  border: none;
  outline: none;
}

.deposit-button {
  background-color: #ffd700;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.deposit-button:hover {
  background-color: #e6c200;
}
